}
.az-header .az-menu-bar .link-box .link:hover span:before{width:102%;-webkit-transform:translate3d(-1%,0,0) scaleX(0);transform:translate3d(-1%,0,0) scaleX(0)
}
.az-header .az-menu-bar .close-icon{position:absolute;top:10%;left:80%;color:#eec578
