}
.az-header .az-menu-bar .close-icon:before{position:absolute;content:"";display:block;height:4px;width:0;top:70%;-webkit-transform:translate3d(-1%,0,0) scaleX(1);transform:translate3d(-1%,0,0) scaleX(1);background:#eec578;-webkit-transition:width .3s ease,-webkit-transform .3s ease-out .3s;transition:width .3s ease,-webkit-transform .3s ease-out .3s;transition:width .3s ease,transform .3s ease-out .3s;transition:width .3s ease,transform .3s ease-out .3s,-webkit-transform .3s ease-out .3s;-webkit-transform-origin:right center;-ms-transform-origin:right center;transform-origin:right center
}
.az-header .az-menu-bar .close-icon:hover{cursor:pointer
